<template>
  <div>
    <el-divider>CALL</el-divider>
    <el-row>
      <el-col span="12">
        <down_can_call></down_can_call>
      </el-col>
      <el-col span="12">
        <csv_upload @on_uploaded="uploaded_can_call"></csv_upload>
      </el-col>
    </el-row>
    <el-divider>CALL</el-divider>
    <el-row>
      <el-col span="12">
        <down_will_call></down_will_call>
      </el-col>
      <el-col span="12">
        <csv_upload @on_uploaded="uploaded_will_call"></csv_upload>
      </el-col>
    </el-row>
    <el-divider>Bonus</el-divider>
    <el-row>
      <el-col span="12" offset="12">
        <csv_upload @on_uploaded="uploaded_bonus"></csv_upload>
      </el-col>
    </el-row>
    <el-divider>Reserve</el-divider>
    <el-row>
      <el-col span="12" offset="12">
        <csv_upload @on_uploaded="uploaded_reserve"></csv_upload>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import down_can_call from "@/components/common/template/down_can_call";
import down_will_call from "@/components/common/template/down_will_call";
import csv_upload from "@/components/common/uploader_csv";
import axios from "axios";

export default {
  data() {
    return {
      token: sessionStorage.getItem("token")
    };
  },
  components: { down_can_call, down_will_call, csv_upload },
  methods: {
    uploaded_reserve(d) {
      this.$confirm("  ", {
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        axios({
          url: require("@/config").axios.import_reserve,
          method: "post",
          data: { token: this.token, info: { filename: d } }
        }).then(re => {
          this.$message("");
        });
      });
    },
    uploaded_bonus(d) {
      this.$confirm("  ", {
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        axios({
          url: require("@/config").axios.import_bonus,
          method: "post",
          data: { token: this.token, info: { filename: d } }
        }).then(re => {
          this.$message("");
        });
      });
    },
    uploaded_can_call(d) {
      this.$confirm(" CALL ", {
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        axios({
          url: require("@/config").axios.import_cancall,
          method: "post",
          data: { token: this.token, info: { filename: d } }
        }).then(re => {
          this.$message("");
        });
      });
    },
    uploaded_will_call(d) {
      this.$confirm(" CALL ", {
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        axios({
          url: require("@/config").axios.import_willcall,
          method: "post",
          data: { token: this.token, info: { filename: d } }
        }).then(re => {
          this.$message("");
        });
      });
    }
  }
};
</script>

